var graceRichText = require("../../static/richText.js");
export default {
	
	data() {
		return {
			bannerDetails:[],//轮播图详情信息
			contentHtml:'',//后台返回的HTML代码
			
		};
	},
	
	async onLoad(option){
		let self = this;
		let id = option.id;
		console.log(id)
		self.$axios(self.$baseUrl.getnew,{
			new_id:id
		})
		.then(res =>{
			console.log(res.data.data)
			if(res.data.code == 1){
				self.bannerDetails = res.data.data.newdata;
				 // 强化 richText
				self.contentHtml = graceRichText.format(res.data.data.newdata.content);
				
			}
			
		},(error) =>{
			console.log(error)
		})
		
	},
	methods:{
		
	}
	
}
